/**
 * documentSize  AspectRatio       Capture   EdgesDetection .
 *   :
 * 1.  AspectRatio  documentSize.           AspectRation,  
 *               documentSize.
 * 2.   documentSize.    AspectRatio   DucumentSize- .
 * 3.   AspectRatio.         ,   
 *         -      .
 */
export class CDocumentHints {
  constructor({
    aspectRatioMin = 0,
    aspectRatioMax = 0,
    focalLength = -1,
    documentSize = {
      width: 0,
      height: 0,
    },
  }) {
    this.aspectRatioMin = aspectRatioMin;
    this.aspectRatioMax = aspectRatioMax;
    this.focalLength = focalLength;
    this.documentSize = documentSize;
  }

  public aspectRatioMin: number; //    
  public aspectRatioMax: number; //    
  /**   :
   *   - ( AspectRatioMin == 0.0 && AspectRatioMax == 0.0 ) - AspectRatio  
   *   - ( AspectRatioMin <= AspectRatioMax ) && ( AspectRatioMin >= 1.0 && AspectRatioMax >= 1.0 ) - 
   *          AspectRatio
   *       EC_InvalidArgument
   */
  public focalLength: number; //    (      )
  /**   ( focalLength < 0.0 ),          
   *    ( focalLength == 0.0 ),        ,    
   *    ( focalLength > 0.0 ),       
   */
  public documentSize: {
    //  .
    /** width  height      ,    ,     ,
     *       .
     */
    width: number;
    height: number;
  };
}
